import React from "react"
import { Link, StaticQuery, graphql } from "gatsby"
import Img from "gatsby-image"

import "./../styles/place-graphic.scss"

export default class FrontGraphic extends React.Component {
    render() {
        return (

<StaticQuery
    query={graphql`
        query {
            allFile(filter: {relativeDirectory: {eq: "img"}}) {
                edges {
                    node {
                        relativePath
                        childImageSharp {
                            fixed(width: 700) {
                                ...GatsbyImageSharpFixed
                            }
                        }
                    }
                }
            }
        }
    `}
    render={data => {
        const place_data = [
            {
                name: 'American South',
                filename: '_MG_7576.jpg',
                link: '/united-states/american-south',
            },
            {
                name: 'American Southwest',
                filename: '_MG_5846.jpg',
                link: '/united-states/american-southwest',
            },
            {
                name: 'Baltimore, MD',
                filename: 'IMG_6765.jpg',
                link: '/united-states/baltimore',
            },
            {
                name: 'Brooklyn, NY',
                filename: '_MG_7333.jpg',
                link: '/united-states/brooklyn',
            },
        ]
        return (
            <div id="place-graphic-wrapper" className="places-us">
            {
                place_data.map((place, index) =>
                    <div className="place-link" key={index}>
                        <Link to={"/places" + place.link}>
                            <Img
                                fixed={data.allFile.edges.find(({node}) => node.relativePath === 'img/' + place.filename).node.childImageSharp.fixed}
                                style={{width: '100%', height: '100%'}}
                            />
                            <div className="caption">
                                <div className="caption-inner">
                                    <span className="border inner-right"></span>
                                    <span className="border inner-left"></span>
                                    <span className="border inner-bottom"></span>
                                    <span className="border inner-top"></span>
                                    {place.name}
                                </div>
                            </div>
                        </Link>
                    </div>
                )
            }
            </div>
        )
    }}
/>

           )
    }
}

